@charset 'utf-8';

@media (prefers-reduced-motion: no-preference) {
	html {
		scroll-behavior: smooth;
	}
}

body .n-contain {
	max-width: 1312px;
}

:root {
	--color-teal: #488e89;
}

.n-carousel {
	background: #eee;
}

.n-wp .n-carousel > .n-carousel__index.n-carousel__index--thumbnails > * span {
	padding: 0;
}

.n-wp .n-carousel.n-carousel--lightbox {

	grid-template-areas:
		"f c a3 a3 a3 a3"
		"prev a2 a2 a2 a2 next"
		"v v v v v v"
		"i i i i i i";

	> .n-carousel__content {
		grid-row: 1/-2;
	}

	&.n-carousel--overlay {
		margin: 0;
		grid-template-areas:
			"f c a3 a3 a3 a3"
			"prev a2 a2 a2 a2 next"
			"i i i i i i";

		> .n-carousel__content {
			grid-row: 1/-1;
		}

		// .n-aspect {
		// 	display: contents;
		// }

		.gallery {
			flex-wrap: nowrap;
			margin: 0;
			overflow: auto;
			overflow-y: hidden;

			a {
				width: 2em;
			}
		}
	}

	&:-webkit-full-screen {
		margin: 0;
		grid-template-areas:
			"f c a3 a3 a3 a3"
			"prev a2 a2 a2 a2 next"
			"i i i i i i";

		> .n-carousel__content {
			grid-row: 1/-1;
		}

		// .n-aspect {
		// 	display: contents;
		// }

		.gallery {
			flex-wrap: nowrap;
			margin: 0;

			a {
				width: 2em;
			}
		}
	}

	&:fullscreen {
		margin: 0;
		grid-template-areas:
			"f c a3 a3 a3 a3"
			"prev a2 a2 a2 a2 next"
			"i i i i i i";

		> .n-carousel__content {
			grid-row: 1/-1;
		}

		// .n-aspect {
		// 	display: contents;
		// }

		.gallery {
			flex-wrap: nowrap;
			margin: 0;

			a {
				width: 2em;
			}
		}
	}

}

.n-carousel__content.featured {
	margin-bottom: 22px;
}

.n-carousel__content.featured h2 a[href] {
	position: absolute;
	display: block;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

.n-carousel .n-carousel__content.featured {
	margin-bottom: 0;
}

.n-carousel__content.featured > * {
	position: relative;
	text-align: center;
	padding: 9% 0;
	overflow: visible;
}

.n-carousel__content.featured a[href],
.n-carousel__content.featured p {
	color: #fff;
}

.n-carousel__content.featured > div > * {
	padding: 0 0.5em;
	display: inline-block;
	background: rgba(0, 0, 0, 0.5);
	color: #fff;
}

.n-wp .n-carousel__content.featured img {
	display: none;
}

.n-wp .n-carousel__content.featured picture img[class*="thumbnail"] {
	position: absolute;
	z-index: -1;
	margin: auto;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	max-height: none;
	padding: 0;
	display: block;
}

.n-wp .sidebar form {
	margin: 0;
	padding: 0;
}

.n-wp img {
	display: block;
	max-width: 100%;
	height: auto;

	:not(.avatar) {
		margin: 0 auto;
	}
}

.n-wp .n-aspect img,
.n-wp .wp-caption > img {
	display: inline-block;
}

.n-wp .sidebar ul,
.n-wp .sidebar li,
.n-wp .comments ul,
.n-wp .sidebar li a[href] {
	list-style: none;
	padding-left: 0;
	margin: 0;
	display: block;
}

.n-wp .sidebar li {
	margin-bottom: 0.5em;
}

button.search-submit {
	display: none;
}

.n-wp .n-carousel--lightbox {

	margin-top: 1.5em;

	.n-aspect {
		max-height: 100%;
		display: flex;
	}

	.gallery {
		margin: 0.5em -0.7em;
		display: flex;
		flex-wrap: wrap;
		max-width: 100%;
		margin: 1rem 0;
		justify-content: flex-start;
		max-height: 20em;
		overflow: hidden;
		position: relative;
		max-width: 100%;
		width: 100%;

		-webkit-mask-image: -webkit-linear-gradient(270deg, black, black 95%, transparent); // Chrome
		-webkit-mask-size: 100% 20em;
		-webkit-mask-repeat: no-repeat;
		mask-image: linear-gradient(180deg, black, black 95%, transparent); // Safari, Firefox
		mask-size: 100% 20em;
		mask-repeat: no-repeat;

		a {
			display: block;
			width: 20%;
			margin: 0;
			border: 1px solid transparent;
			text-align: center;
			box-sizing: border-box;

			&:not([aria-current]) {
				--nui-control-bg: transparent;
			}

			img {
				display: inline-block;
				vertical-align: middle;
				width: auto;
				height: auto;
			}
		}

	}

	&.n-carousel--overlay {

		.gallery {
			justify-content: center;
		}

	}

	&.n-carousel--inline:not(.n-carousel--overlay) {
		.gallery [aria-current] {
			--nui-control-bg: transparent;
		}
	}
}

.n-wp .n-carousel--lightbox .gallery body {
	min-height: 100%;
	display: flex;
	flex-direction: column;
}

.n-wp.n-type h2 a[href] {
	color: #fff;
}

#full-window .n-carousel__previous,
#full-window .n-carousel__next {
	top: 49%;
}

.wp-caption,
iframe {
	max-width: 100%;
}

iframe,
article h2 a {
	margin: 0 auto;
	display: block;
}

.home article,
.archive article,
.search-results article {
	margin-bottom: 44px;
	position: relative;
	//	overflow: hidden;
}

.n-type.n-wp section > article > {

	p:not(.has-image):not(.has-iframe),
	ol,
	ul,
	blockquote {
		max-width: 40em;
		margin: 1.5em auto;
		box-sizing: border-box;
	}

	p.has-image {
		text-align: center;

		//		font-size: 0; // Disable legacy images' captions, because they appear in the wrong place
		img {
			display: inline-block;
		}
	}

	.headline + p.has-image {
		margin-top: 0.5em;
	}
}

article {
	ul {
		padding-left: 2px;
	}

	.n-aspect {
		// background-size: contain;
		// background-repeat: no-repeat;
		// background-image: var(--placeholder, none);
		z-index: 0;
		position: relative;
		overflow: hidden;
		// display: inline-flex; // breaks aspect-ratio

		&:before {
			content: "";
			background-size: contain;
			background-repeat: no-repeat;
			background-image: var(--placeholder, none);
			position: absolute;
			inset: 0;
			filter: blur(4px);
			z-index: -1;
		}
	}
}

.comments {
	li {
		margin-bottom: 2em;
	}

	p {
		width: 100%;
		margin-bottom: 0;
	}

	.n-form {
		padding: 0;
	}

	.n-form.n-form__wide label span {
		display: inline;
	}

	.comments form input {
		border: 0;
	}

	.comment-meta.commentmetadata {
		font-size: 0.75em;
	}

	.n-form label {
		min-height: 2em;
	}

	.comment-body {
		padding-right: 1em;
	}

	textarea {
		width: 100%;
	}
}

.n-wp .img-crop {
	width: 100%;
	display: flex;
	flex-direction: column;
	background-color: transparent;
	overflow: hidden;
	position: relative;
	white-space: nowrap;

	&:before {
		display: inline-block;
		vertical-align: middle;
		content: "";
		padding-top: 25vmin;
	}

	&:after {
		content: "";
		position: absolute;
		inset: 0;
		background: var(--placeholder, none) center no-repeat;
		background-size: cover;
		filter: blur(4px);
		overflow: hidden;
	}

	img {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}
}

.img-crop h2,
article h2 a[href] {
	display: inline-block;
	vertical-align: bottom;
	padding: 0.5em;
	font-size: 1.5rem;
	background: var(--color-teal);
	color: #fff;
	text-decoration: none;
}

article h2 a[href]:after {
	content: " â†’";
}

.comment-author img {
	padding-bottom: 0;
}

