nav.n-nav {
	a {
		color: var(--nui-control-color, #666);
		text-transform: uppercase;
		text-decoration: none;

		&.active[href],
		&[href]:hover {
			color: var(--nui-control-highlight, #ee8822);
		}
	}

	&.n-nav--drop {
		--nui-control-bg: #eee;

		li.current_page_item {
			--nui-control-bg: #ddd;
		}
	}
}

.single h1 {
	margin-top: 0.25em;
}

.n-type {
	h3.comment-reply-title {
		padding-top: 0;
	}

	a[href] {
		color: lightcoral;
		text-decoration: none;
	}

	a[href]:hover,
	input[type="submit"]:hover,
	a.n-btn[href]:hover,
	button.n-btn:hover {
		color: var(--nui-control-highlight, #ee8822);
	}

	.page_item a[href] {
		color: unset;
		padding: 0 0.5em;
		line-height: 1.5;
	}

	p {
		text-align: left;
	}

	article {

		h1,
		h2,
