			line-height: 1.33;
		}

		.img-crop {
			h2 {
				color: #fff;
				position: relative;
				white-space: normal;
				line-height: 1.33;
				background: rgba(72, 142, 137, 0.88);
				transition: background-color 0.2s ease-in-out;
				margin-bottom: 0;
				z-index: 1;
			}

			&:hover h2 {
				background: rgb(72, 142, 137);
			}
		}
	}
}

.wp-block-image {
