// Image captions
.wp-caption {
	margin: 0.5em auto;

	a[href] {
		display: inline-block;
		max-width: 100%;
	}

	&.alignright {
		margin: 0 0 0 auto;
	}

	p.wp-caption-text {
		margin: 0;
		font-size: 12px;
		line-height: 24px;
		padding: 0;
	}
}

a.n-backtotop[href] {
	background: none;
	bottom: -1em;
}

.textwidget a {
	margin-bottom: 0.5em;
	display: inline-block;
}

@media (min-width: 600px) {
	nav.n-nav.n-nav--drop ul li {
		margin-right: 0.5em;
	}

	.home section {
		display: flex;
		flex-wrap: wrap;
	}

	.home section article {
		width: calc(50% - 0.75em);
		margin: 0 0 3em 0;
	}

	.home section article:nth-child(2) ~ :nth-child(2n + 1),
	.home.paged section article:first-child {
		margin-right: 1.5em;
	}

	.home:not(.paged) section article:nth-child(1),
	.home:not(.paged) section article:nth-child(2),
	.home section .pagination {
		width: 100%;

		.n-carousel--lightbox .gallery a {
			// Retina thumbnails
			width: 10%;
		}

		.img-crop {
			background-size: cover;
		}

	}

	.n-wp .n-carousel--lightbox .gallery a {
		// Retina thumbnails
		width: 10%;
	}

	.comments {
		display: grid;
		grid-template-columns: 1fr 1fr;

		> h2 {
			grid-column: 1 / 3;
		}

		> ul {
			grid-column: 1 / 2;
		}

		> div {
			grid-column: 2 / 3;
		}
	}
}

.n-nav--drop li[aria-haspopup] {
	padding-right: 1em;
}

.wp-video {
	margin: 1.5em auto 0 auto;
}

@media (max-width: 599.9px) {
	.single h1 {
		font-size: 1.5em;
	}
}

// Color scheme
.n-carousel__previous,
.n-carousel__next,
.n-carousel__full-screen,
.n-carousel__close,
.n-carousel__index .n-tool:after,
.n-btn,
.n-form input[type="submit"],
.n-form button[type="submit"],
.n-form button[type="reset"],
button.n-btn,
a.n-btn[href],
.n-type a.n-btn[href],
.n-accordion__label,
.n-nav--drop,
.n-form,
.n-ovrl > .n-ovrl--close,
.n-nav--drop ul li,
.n-type nav.n-nav a[href] {
	--nui-control-bg: lightcoral;
	--nui-control-color: #fff;
	--nui-control-active-bg: #cc7c7e;
	--nui-control-active-color: #eee;
	--nui-control-highlight: #bf2a2a;
}

.n-form input,
.n-form textarea {
	--nui-input-color: darkslateblue;
	--nui-input-bg: aliceblue;
}

// Auto dark mode
@media (prefers-color-scheme: dark) {

	body,
	.n-ovrl > *,
	.n-carousel {
		background: #000;
	}

	.n-type {
		color: #ddd;
	}

	.img-crop {
		background-color: #222;
	}

	.n-form input,
	.n-form textarea {
		--nui-input-color: #ddd;
		--nui-input-bg: #222;
	}

	.logo .default-logo {
		background: url(img/logo-white.svg) top left no-repeat;
		background-size: contain;

		img {
			opacity: 0;
		}
	}
}

